import React from "react";
import Grid from "@material-ui/core/Grid";

import { useTrail, animated } from "react-spring";

import data from "../data/inventory";
import selectedItems from "../data/selecteditems";

import MediaControlCard from "./SelectedItem";

export default () => {
  const items = data.products.filter(product =>
    selectedItems.items.includes(product.name)
  );
  const trail = useTrail(items.length, {
    top: 0,
    opacity: 1,
    from: { top: 40, opacity: 0 }
  });
  return (
    <div>
      <Grid container spacing={3}>
        {trail.map(({ top, opacity }, index) => (
            <Grid item xs={12} sm={6}>
            
            </Grid>

        ))}
      </Grid>
    </div>
  );
};
